* { font-family: Verdana, Geneva, sans-serif; font-size: 15px; font-style: normal; font-variant: normal; font-weight: 500; line-height: 26.4px; }


/* Estilo da barra de rolagem */
.selector-info >select {
    /* Adicionando uma altura e largura máxima para a div com a barra de rolagem */
    max-height: 300px;
    max-width: 500px;

    /* Definindo a barra de rolagem */
    overflow-y: scroll;
    /* Adiciona a barra de rolagem vertical */
    scrollbar-width: thin;
    /* Largura da barra de rolagem - "thin" para uma barra fina */
}

/* Personalizando a aparência da barra de rolagem */
.selector-info >select::-webkit-scrollbar {
    width: 8px;
    /* Largura da barra de rolagem para navegadores WebKit (Chrome, Safari, etc.) */
}

.selector-info >select::-webkit-scrollbar-thumb {
    background-color: #4a5568;
    /* Cor do polegar (thumb) da barra de rolagem */
    border-radius: 8px;
    /* Borda arredondada para o polegar */
}

.selector-info >select::-webkit-scrollbar-track {
    background-color: #cbd5e0;
    /* Cor da trilha da barra de rolagem */
    border-radius: 8px;
    /* Borda arredondada para a trilha */
}


